@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Inter:wght@200;300;400;500;600;700;800;900&family=Montserrat:ital,wght@0,300;0,400;1,400&family=Tilt+Warp&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Inter:wght@300;400;500;600;700;800;900&family=Montserrat:ital,wght@0,300;0,400;1,400&family=Poppins&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
}

:root {
  --white: #ffffff;
  --blue: #40578f;
  --font-color: #8892b0;
  --light-orange: #e55f3f;
  --light-yellow: #f1a845;
  --background: #001528;
  --background-secondary: #011a31;
}

body {
  font-family: "Tilt Warp", cursive;

  color: white;
  background-color: #001528;
  overflow-x: hidden;
}

a {
  color: inherit;
  text-decoration: inherit;
}

ul,
ol {
  list-style: none;
}
